body{
  height: 100vh;
}
.footer-primary-color {
  color: #a0feea;
}
.btn {
  padding: 1rem 4rem;
  z-index: 1;
  border-radius: 0.8rem;
  font-size: 18px;
  font-family: 'Muli', sans-serif;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.fixed {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 10;
}

.partner {
  border-top: 3px solid #f9f5f5;
  padding-top: 5rem;
}

.border--top {
  border-top: 3px solid #f9f5f5;
}

