}
.topbar{
  position: fixed;
  height:40px;
  left:0;
  top:0;
  width:100%;
  text-align:center;
  font-size:22px;
  -webkit-transition:top .3s;
  transition:top .3s;
  z-index: 2;
}

.hide{
  top:-40px;
  background-color: #fff;
  box-shadow: 0px 4px 4px rgba(0,0,0,0.2);
}

.girl-absolute {
  position: absolute;
  bottom: -195px;
